import { createContext, FC, useReducer } from "react";

export interface Todo{
    value:string,
    id:string
}

function Reducer(state:Todo[], action:actionType){
    switch (action.type) {
        case "add":
            return [...state, action.payload];
        
        case "remove":
            return state.filter(todo=>todo.id !== action.payload.id);

        default:
            return state;
    }
}

const defaultValue = {
    state:[],
    dispatch:()=>{}
}

interface propsType{
    children:JSX.Element
}

interface actionType{
    type:string,
    payload:Todo
}




export const ActiveContext = createContext<{state:Todo[], dispatch:React.Dispatch<actionType>}>(defaultValue);
export const CompletedContext = createContext<{state:Todo[], dispatch:React.Dispatch<actionType>}>(defaultValue);




export const ActiveProvider:FC<any> = (props:propsType)=>{
    let [state, dispatch] = useReducer(Reducer, []);

    return (
        <ActiveContext.Provider value={{state, dispatch}}>
            {props.children}
        </ActiveContext.Provider>
    )
}


export const CompletedProvider:FC<any> = (props:propsType)=>{
    let [state, dispatch] = useReducer(Reducer, []);
    return (
        <CompletedContext.Provider value={{state, dispatch}}>
            {props.children}
        </CompletedContext.Provider>
    )
}