import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import { useContext, useState } from "react";
import { ActiveContext } from "../Store";

function Input() {
    // const dispatch = useDispatch();
    let [error, setError] = useState(false);
    let [value, setValue] = useState("");
    let active = useContext(ActiveContext);
    function addTodo() {

        if (value.trim() === "") {
            setError(true);
            return;
        }
        setError(false);
        active.dispatch({type:"add", payload:{value, id:`${Math.random()}`}});
        setValue("");
    }
    return (
        <div style={{ width: "100%", display: "flex", justifyContent: "space-evenly", alignItems: "center" }}>
            <TextField 
                label="Add a task" 
                onKeyDown={(e)=>{
                    setError(false);
                    if(e.key === "Enter")
                        addTodo();
                    return;
                }}
                error={error} value={value} 
                onChange={(e) => setValue(e.target.value)} 
                helperText={(error) ? "Enter a task to add" : ""} 
                fullWidth={true} 
                variant="standard" 
                sx={{ marginRight: "10px" }} 
            />

            <Button variant="contained" sx={{ marginLeft: "10px", height: "40px" }} onClick={addTodo}>
                Add
            </Button>
        </div>
    )
}


export default Input;