import Paper from "@mui/material/Paper";
import Toolbar from "@mui/material/Toolbar";
import ListItem from "@mui/material/ListItem";
import ListItemText from "@mui/material/ListItemText";
import IconButton from "@mui/material/IconButton";
import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';
import CheckIcon from '@mui/icons-material/Check';
import { useContext } from "react";
import { ActiveContext, CompletedContext, Todo } from "../Store";

function ActiveLists() {

    function handleCheck(item:Todo){
        activeLists.dispatch({type:"remove", payload:item});
        dispatch({type:"add", payload:item});

    }
    
    function handleDelete(item:Todo){
        activeLists.dispatch({type:"remove", payload:item});
    }
    
    function getIconButtons(item:Todo){
        return(
            <>
                <IconButton onClick={()=>handleCheck(item)} title="mark done" edge={"end"} key={"k"}>
                    <CheckIcon />
                </IconButton>
    
                <IconButton onClick={()=>handleDelete(item)} title="remove" edge={"end"} key={"d"}>
                    <DeleteOutlineIcon />
                </IconButton>
            </>
    
        )
    }
    
    function getTodoElement(item:Todo){
        return (
            <Paper draggable={true} onDragStart={(e)=>console.log("drag start",e)} onDragEnd={(e)=>console.log("drag end",e)} elevation={1} sx={{ marginY: "5px", width: "98%", marginX: "auto" }} key={`${Math.random()}`}>
                <ListItem secondaryAction={getIconButtons(item)}>
                    <ListItemText primary={item.value} />
                </ListItem>
            </Paper>
        )
    }
    


    let activeLists = useContext(ActiveContext);
    let { dispatch } = useContext(CompletedContext);

    return (
        <Paper key={"Active Tasks"} sx={{ width: { md: "100%" }, marginY: "40px", margin: { md: "0 10px 0 0" }, textAlign: "center", border: "1px solid rgba(128, 128, 128, 0.5)", borderBottom: "1px solid rgba(128, 128, 128)" }}>
            <Toolbar sx={{ backgroundColor: "rgba(128, 128, 128, 0.06)", borderBottom: "1px solid rgba(128, 128, 128, 0.3)", fontSize: "large", display: "flex", justifyContent: "center" }}>
                Active Tasks
            </Toolbar>
            <div style={{ maxHeight: "50vh", overflow: "auto" }}>
                
                {
                    activeLists.state.map((todo)=>getTodoElement(todo))
                }

            </div>
        </Paper>
    )
}


export default ActiveLists;
