import React from 'react';
import ReactDOM from 'react-dom/client';
import App from './App';
import { ActiveProvider, CompletedProvider } from "./Store";

const root = ReactDOM.createRoot(
  document.getElementById('root') as HTMLElement
);
root.render(
  <React.StrictMode>
    <CompletedProvider>
      <ActiveProvider>
        <App />
      </ActiveProvider>
    </CompletedProvider>
  </React.StrictMode>
);