import Paper from "@mui/material/Paper";
import Toolbar from "@mui/material/Toolbar";
import ListItem from "@mui/material/ListItem";
import ListItemText from "@mui/material/ListItemText";
import IconButton from "@mui/material/IconButton";
import { CompletedContext, Todo } from "../Store";
import Close from "@mui/icons-material/Close";
import { useContext } from "react";

function CompletedLists() {

    function handleClose(item:Todo){
        dispatch({type:"remove", payload:item});
    }
    
    function getIconButtons(item:Todo){
        return(
            <IconButton title="remove" edge={"end"} onClick={()=>handleClose(item)}>
                <Close />
            </IconButton>
    
        )
    }
    
    function getTodoElement(item:Todo){
        return (
            <Paper elevation={1} sx={{ marginY: "5px", width: "98%", marginX: "auto" }} key={`${Math.random()}`}>
                <ListItem secondaryAction={getIconButtons(item)}>
                    <ListItemText primary={item.value} />
                </ListItem>
            </Paper>
        )
    }



    let {state, dispatch} = useContext(CompletedContext);

    return (
        <Paper key={"completed Tasks"} sx={{ width: { md: "100%" }, marginY: "40px", margin: { md: "0 0px 0 10px" }, textAlign: "center", border: "1px solid rgba(128, 128, 128, 0.5)", borderBottom: "1px solid rgba(128, 128, 128)" }}>
            <Toolbar sx={{ backgroundColor: "rgba(128, 128, 128, 0.06)", borderBottom: "1px solid rgba(128, 128, 128, 0.3)", fontSize: "large", display: "flex", justifyContent: "center" }}>
                Completed Tasks
            </Toolbar>
            <div style={{ maxHeight: "50vh", overflow: "auto" }}>

                {
                    state.map(todo=>getTodoElement(todo))
                }

            </div>
        </Paper>
    )

}


export default CompletedLists;
